export default {
	props: {
		tabBars: Array,
	},
	data() {
		return {
			loading: false
		}
	},
	methods: {
		tijiao(id) {
			// id,orderStatus  16  releaseType 4  shouhuo
			//(long id, long saleUserId, int orderStatus  16, int releaseType 4);
			let uuidform = {
				uuid: this.$http.getUuid(),
				id: id,
				orderStatus: 16,
				releaseType: 4
			};
			this.loading = true;
			this.$http.get(this.$urlconfig.shouhuo, uuidform, {}).then(data => {
				this.loading = false;
				//{status: 1, msg: "不能重复报价", data: null, success: false}
				if (data.status === 0) {
					uni.showToast({
						title: data.data,
						icon: "none"
					});
					this.$emit("getbaojiaL");
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					});
				}
			});

		}
	}
}
